.video-container {
    flex: 1 1;
    min-width: 200px;
    max-width: 600px;
    padding: 20px;
    margin: auto;
}

.aspect-container {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
}

.aspect-content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    text-align: center;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}